import "./../header/Header.scss"
import { NavLink } from "react-router-dom";

export default function Header(props) {

  const arr = ['Home','Orders','Cart','Profile'];


  return (
    <div className="header">
      <div className="navBar">
        <h1 style={{color:"white"}}>My Ecomm</h1>
        <ul>{arr.map((header)=>{return <NavLink to={"/"+header} className="menu-item">
          {header}
        </NavLink>})}</ul>
      </div>
    </div>
  );
}
